import logo from './logo.svg';
import React, {useState, useEffect} from 'react'
import PP from './Policy-Documents/privacypolicy.md'
import TOS from "./Policy-Documents/TOS.md";
import Del from "./Policy-Documents/Del.md";

import Markdown from 'markdown-to-jsx';
import './App.css';

function App() {
  const [buttonNum, setButtonNum] = useState(1)
  const [markdown, setMarkDown] = useState("")

  useEffect(() => {
    if(buttonNum === 1){
        fetch(PP)
        .then((res) => res.text())
        .then((text) => setMarkDown(text));
    }else if (buttonNum === 2){
        fetch(TOS)
        .then((res) => res.text())
        .then((text) => setMarkDown(text));
    }else if(buttonNum === 3){
      fetch(Del)
      .then((res) => res.text())
      .then((text) => setMarkDown(text));
      }
  },[buttonNum])

  return (
    <div className="App">
      <h1> Policies for the upcoming www.roombae.com </h1>
      <h2> Website awaiting policy verification for productionalisation </h2>
      <header className="App-header">
          <button onClick = {() => setButtonNum(1)}>Privacy Policy</button>
          <button onClick = {() => setButtonNum(2)}>Terms of service</button>
          <button onClick = {() => setButtonNum(3)}>Delete Profile Instructions</button>
      </header>
      <div style = {{padding: "20px", display: "flex"}}>
        <Markdown>
          {markdown}
        </Markdown>
      </div>
    </div>
  );
}

export default App;
